<template>
  <div class="reducedPackaging flex-page">
    <navigation-bar @onLeftClick="goBack" :pageName="title">
      <slot slot="nav-right" class="rightBar" name="right">
        <div class="rightBar" @click="goToShopCart">
          <img
            ref="cartIcon"
            class="shopIcon"
            src="@/assets/images/newSlicing/icon_shop_cut.png"
            alt
          />
          <div
            class="goodsNum"
            :class="skuTypeNum>=100?'goodsNumMax':''"
            v-show="skuTypeNum!=0"
          >{{skuTypeNum<=99?skuTypeNum:'99+'}}</div>
        </div>
      </slot>
    </navigation-bar>
    <div class="post-wrapper" ref="contentScroll">
      <div class="goodsList">
        <div
          class="goodsItem"
          v-for="(goodsItem,goodsIndex) in goodsList"
          :key="'goodsItem'+goodsIndex"
          @click="goToProduct(goodsItem)"
        >
          <div class="goodsImg">
            <img :src="goodsItem.spuPicUrl" alt />
            <div class="outSellBox" v-show="goodsItem.storeNumber==0">补货中</div>
            <div
              class="jinshengNum"
              v-show="goodsItem.storeNumber<=goodsItem.threshold&&goodsItem.storeNumber!=0"
            >仅剩{{goodsItem.storeNumber}}件</div>
            <div class="fullIcon" v-if="goodsItem.listLabel">{{goodsItem.listLabel}}</div>
          </div>
          <div class="goodsInfo">
            <div class="goodsName">{{goodsItem.spuName}}</div>
            <div class="goodsDesc">{{goodsItem.spuSalePoint}}</div>
            <div class="goodsSale">月售{{goodsItem.virtualSale | threshold}}</div>
            <div class="goodsPriceBx">
              <div class="goodsPriceInfo">
                <div class="goodsPrice">
                  <div class="goodsPriceIcon">￥</div>
                  <div class="goodsPriceNum">{{goodsItem.skuPrice|priceFilters}}</div>
                </div>
                <div
                  class="goodsLinePrice"
                  v-if="goodsItem.priceLine"
                >￥{{goodsItem.priceLine|priceFilters}}</div>
              </div>
              <div class="addMinusBtn">
                <div class="clickBox" @click.stop="addGoodsNum(goodsItem,goodsIndex)"></div>
                <div class="cartNumBox" v-show="goodsItem.cartCount!=0">
                  <div class="cartNum">{{goodsItem.cartCount}}</div>
                </div>
                <img class="addBtn" ref="addBtn" src="@/assets/images/mall/mall_icon_add.png" alt />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="fixedHeight" :style="{height:30+44+this.$footBarHeight+'px'}"></div>
    </div>
    <transition @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
      <img class="circle" v-show="isShowCircle" src="@/assets/images/mall/mall_icon_add.png" alt />
    </transition>
    <!-- <netWorkError v-show="ifNetWorkError"></netWorkError> -->
  </div>
</template>

<script>
import { getActivityTepmlate } from "../../services/productInfoService";
import {
  getActSpuListService,
  getUnionPayActSpuList,
  getSkuTypeNumService,
  addGoodsService
} from "../../services/goodsService";
import NavigationBar from "@/common/currency/NavigationBar.vue";
import { mapActions, mapState } from "vuex";
import { Toast, Dialog } from "vant";
import Vue from "vue";
import netWorkError from "../../common/networkError/NetworkError.vue";

export default {
  data() {
    return {
      templateId: "",
      title: "",
      sourceList: {
        title: ""
      },
      ifNetWorkError: false, //网络异常标志
      skuTypeNum: null,
      templateId: "", //活动id
      goodsList: null, //商品列表
      scrollContentTop: null,
      addBtnIcon: null,
      addGoodsLoading: false,
      isShowCircle: false
    };
  },
  components: {
    netWorkError,
    NavigationBar
  },
  filters: {
    threshold(data) {
      return data >= 10000
        ? data / 10000 >= 10
          ? "9万+"
          : Math.floor(data / 10000) + "万+"
        : data;
    },
    priceFilters(data) {
      return data >= 10000
        ? Math.floor((data / 10000) * 100) / 100 + "万"
        : data;
    }
  },
  methods: {
    //获取活动模板信息
    getActivityTepmlate() {
      const data = {
        id: this.templateId
      };
      getActivityTepmlate(data)
        .then(Response => {
          this.getActSpuList();
          this.sourceList = Response.data;
          this.title = this.sourceList.title;
          this.ifNetworkError = false;
        })
        .catch(error => {
          this.ifNetworkError = true;
        });
    },
    /** 根据活动编码查活动商品
     * actCode   活动编码
     * ownerCode 业主编码
     * */
    getActSpuList() {
      let paramData = {
        id: this.templateId,
        ownerCode: this.$userInfo.OWNER_CODE,
        areaCode: this.$userInfo.AREA_CODE
      };
      getUnionPayActSpuList(paramData)
        .then(res => {
          if (res.resultCode == "0") {
            this.goodsList = res.data;
          } else {
            this.$toast(res.msg);
          }
        })
        .catch(error => {
          this.$toast(error);
        });
    },
    /**
     * 查询购物车数量
     * ownerCode  Number  业主CODE
     */
    getSkuTypeNum(isAddGoods = false) {
      let paramData = {
        ownerCode: this.$userInfo.OWNER_CODE
      };
      getSkuTypeNumService(paramData)
        .then(res => {
          if (res.resultCode == 0) {
            if (isAddGoods) {
              Toast({
                message: "添加购物车成功",
                duration: 1000
              });
            }
            this.skuTypeNum = res.data.skuTypeNum;
          }
        })
        .catch(error => {});
    },
    // 进入前状态
    beforeEnter(el) {
      console.log(this.addBtnIcon.getBoundingClientRect());
      el.style.left = this.addBtnIcon.getBoundingClientRect().left + "px";
      el.style.top = this.addBtnIcon.getBoundingClientRect().top + "px";
      el.style.transform = "scale(1)";
    },
    // 进入中
    enter(el, done) {
      // el.offsetWidth 强制html渲染动画
      // el.offsetWidth 这句话如何不写就不会有动画效果直接渲染的
      el.offsetWidth;
      el.style.left = this.$refs.cartIcon.getBoundingClientRect().left + "px";
      el.style.top = this.$refs.cartIcon.getBoundingClientRect().top + "px";
      el.style.transform = "scale(.5)";
      done();
    },
    afterEnter(el) {
      this.isShowCircle = !this.isShowCircle;
    },
    //点击加号，增加商品数量
    addGoodsNum(goodsItem, goodsIndex) {
      console.log(goodsItem);
      if (this.addGoodsLoading || this.isShowCircle) {
        // debugger
        return;
      }
      this.addBtnIcon = this.$refs.addBtn[goodsIndex];
      this.isShowCircle = true;
      this.addGoodsLoading = true;
      let paramData = {
        ownerCode: this.$userInfo.OWNER_CODE,
        spuCode: goodsItem.spuCode,
        skuNo: goodsItem.skuNo,
        shopCode: goodsItem.shopCode,
        activitySpuId: goodsItem.activitySpuId,
        skuCount: 1,
        mark: 1
      };
      addGoodsService(paramData)
        .then(res => {
          if (res.resultCode == "0") {
            Toast({
              message: "添加购物车成功",
              duration: 1000
            });
            goodsItem.cartCount += 1;
            this.getSkuTypeNum();
          } else if (res.resultCode == "20100" && res.data.result == "0") {
            this.$toast("您所添加商品超过最大购买量了哦");
          }
          this.addGoodsLoading = false;
        })
        .catch(error => {
          this.addGoodsLoading = false;
        });
      // if (goodsItem.skuShopNum < goodsItem.skuList[0].storeNumber) {
      //     if (goodsItem.skuShopNum < goodsItem.skuCountLimit) {
      //         goodsItem.skuShopNum++;
      //         this.totalMoneyNum += goodsItem.skuList[0].skuPrice;
      //         if (goodsItem.priceLine == "") {
      //             this.lineMoneyNum += goodsItem.skuList[0].skuPrice;
      //         } else {
      //             this.lineMoneyNum += Number(goodsItem.priceLine);
      //         }
      //
      //     } else {
      //         Toast("超过限购数量");
      //     }
      // } else {
      //     Toast("库存不足");
      // }
    },
    //去商品详情页
    goToProduct(goodsItem) {
      this.$router.push({
        name: "goodsDetail",
        params: {
          spuCode: goodsItem.spuCode,
          goodsUrl: goodsItem.spuPicUrl
        }
      });
    },
    goToShopCart() {
      this.$router.push({
        name: "shoppingCart",
        params: {}
      });
    },
    goBack() {
      this.$router.back(-1);
    }
  },
  mounted: function() {},
  activated() {
    console.log(this.sourceList);
    if (this.$route.params.templateId) {
      this.goodsList = [];
      this.title = "";
      this.sourceList = { title: "" };
      this.$refs.contentScroll.scrollTop = 0;
      this.templateId = this.$route.params.templateId;
    } else {
      this.$refs.contentScroll.scrollTop = this.scrollContentTop;
    }
    this.getActivityTepmlate();
    this.getActSpuList();
    this.getSkuTypeNum();
  },
  // computed: mapState({
  //   setActivityCode: state => state.productPage.setActivityCode
  // }),
  // watch: {
  //   setActivityCode: {
  //     handler(newval, oldval) {
  //       if (newval != this.templateId && newval != -1) {
  //         this.templateId = newval;
  //         this.getActivityTepmlate();
  //         this.$store.commit("setActivityCodeChange", {
  //           activityCode: -1
  //         });
  //       }
  //     }
  //   }
  // },
  beforeRouteLeave(to, from, next) {
    this.scrollContentTop = this.$refs.contentScroll.scrollTop;
    if (
      to.name != "goodsDetail" &&
      to.name != "shoppingCart" &&
      to.name != "goodsDetailThemeL" &&
      to.name != "flashSaleGoodsThemeL" &&
      to.name != "flashSaleGoods"
    ) {
    }
    next();
  }
};
</script>
<style scoped lang="scss">
.circle {
  width: 22px;
  height: 22px;
  position: fixed;
  z-index: 100;
  transition: all 0.3s ease;
}
.flex-page {
  width: 100%;
  height: 100%;
  position: absolute;
  .post-wrapper {
    height: 100%;
    overflow-y: scroll;
    background: #f9f9f9;
  }
  .post-wrapper::-webkit-scrollbar {
    display: none;
  }
}
/deep/.navBar {
  height: 44px !important;
  display: flex;
  align-items: center;
  .left,
  .right {
    height: 44px !important;
  }
  .right {
    right: 5px;
    width: 44px !important;
    height: 44px !important;
    /*background: green;*/
    bottom: auto;
  }
}
.rightBar {
  width: 44px;
  height: 44px;
  position: relative;

  img {
    width: 32px;
    height: 32px;
    position: absolute;
    top: 50%; /*偏移*/
    left: 0;
    transform: translateY(-50%);
  }
  .goodsNum {
    width: 16px;
    height: 16px;
    text-align: center;
    border-radius: 0.21333rem;
    letter-spacing: -0.02px;
    background: #fc0238;
    position: absolute;
    top: 0.13333rem;
    left: 0.42667rem;
    font-size: 0.26667rem;
    color: #ffffff;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    min-width: 0.42667rem;
    height: 0.42667rem;
    /*line-height: 0.42667rem;*/
  }
  .goodsNumMax {
    padding: 0 3px;
    min-width: 26px;
  }
}
.activityTitle {
  width: 100%;
  height: 30px;
  text-align: center;
  line-height: 30px;
  background: #f9f9f9;
  font-family: PingFangSC-Regular;
  font-size: 12px;
  color: #fb5364;
  letter-spacing: 0;
}
.goodsList {
  background: #ffffff;
  width: 100%;
  overflow: hidden;
  .goodsItem {
    padding: 15px;
    display: flex;
    .goodsImg {
      width: 100px;
      height: 100px;
      position: relative;
      border-radius: 4px;
      overflow: hidden;
      img {
        width: 100%;
      }
      .showActivitySpu {
        position: absolute;
        top: 0;
        width: 40px;
        height: 16px;
        background: url("../../assets/images/mall/Spike_labelcut.png") no-repeat;
        -webkit-background-size: cover;
        background-size: cover;
        text-align: center;
        line-height: 16px;
        font-family: PingFangSC-Regular;
        font-size: 10px;
        color: #ffffff;
        letter-spacing: 0;
      }
      .jinshengNum {
        width: 100%;
        height: 25px;
        text-align: center;
        position: absolute;
        bottom: 0;
        background: rgba(0, 0, 0, 0.5);
        font-size: 12px;
        color: #bcbcbc;
        letter-spacing: -0.04px;
        line-height: 25px;
      }
      .outSellBox {
        width: 70px;
        height: 70px;
        text-align: center;
        line-height: 70px;
        font-size: 12px;
        color: #ffffff;
        letter-spacing: -0.04px;
        background: rgba(0, 0, 0, 0.5);
        border-radius: 50%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
      .fullIcon {
        position: absolute;
        top: 0;
        left: 0;
        height: 16px;
        padding: 0px 7px;
        text-align: center;
        line-height: 16px;
        font-family: PingFangSC-Regular;
        font-size: 10px;
        color: #ffffff;
        // letter-spacing: 2px;
        background-image: linear-gradient(143deg, #d8ff8a 0%, #66b22b 82%);
        border-radius: 4px 0px 4px 0px;
      }
    }
    .goodsInfo {
      margin-left: 10px;
      .goodsName {
        width: 235px;
        font-family: PingFangSC-Medium;
        font-size: 16px;
        color: #333333;
        letter-spacing: 0;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      .goodsDesc {
        width: 235px;
        font-family: PingFangSC-Regular;
        font-size: 14px;
        color: #888888;
        letter-spacing: 0;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      .goodsSale {
        width: 235px;
        font-family: PingFangSC-Regular;
        font-size: 14px;
        color: #888888;
        letter-spacing: 0;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      .goodsPriceBx {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 15px;
        .goodsPriceInfo {
          display: flex;
          align-items: center;
        }

        .goodsPrice {
          display: flex;
          align-items: center;

          .goodsPriceIcon {
            font-family: PingFangSC-Medium;
            font-size: 14px;
            color: #f43d3d;
            letter-spacing: -0.04px;
          }

          .goodsPriceNum {
            font-family: PingFangSC-Medium;
            font-size: 20px;
            color: #f43d3d;
            letter-spacing: -0.04px;
            line-height: 14px;
          }
        }

        .goodsLinePrice {
          font-family: PingFangSC-Regular;
          font-size: 14px;
          color: #9a9a9a;
          letter-spacing: -0.03px;
          text-align: right;
          line-height: 16px;
          text-decoration: line-through;
          margin-left: 2px;
        }

        .addMinusBtn {
          display: flex;
          position: relative;

          .clickBox {
            width: 55px;
            height: 55px;
            position: absolute;
            top: -17px;
            left: -17px;
          }

          .cartNumBox {
            width: 18px;
            height: 18px;
            position: absolute;
            top: -9px;
            right: -9px;
            background: #fff;
            border-radius: 50%;

            .cartNum {
              width: 16px;
              height: 16px;
              margin: 1px auto 0px;
              box-sizing: border-box;
              border-radius: 50%;
              border: 1px solid #fb5768;
              font-family: PingFangSC-Semibold;
              font-size: 10px;
              color: #fb5768;
              letter-spacing: -0.03px;
              text-align: center;
              line-height: 16px;
            }
          }

          .cartNumBigBox {
            width: 28px;
            height: 18px;
            border-radius: 30px;

            .cartNum {
              width: 26px;
              height: 16px;
              border-radius: 30px;
              margin: 1px auto 0px;
              box-sizing: border-box;
              border: 1px solid #fb5768;
              font-family: PingFangSC-Semibold;
              font-size: 10px;
              color: #fb5768;
              letter-spacing: -0.03px;
              text-align: center;
              line-height: 15px;
            }
          }

          .MinusBtn {
            width: 22px;
            height: 22px;
          }

          .goodsNum {
            width: 30px;
            font-size: 16px;
            color: #333333;
            letter-spacing: -0.06px;
            text-align: center;
            line-height: 22px;
          }

          .addBtn {
            width: 22px;
            height: 22px;
          }
        }
      }
    }
  }
}
</style>